<!-- 布局 -->
<template>
  <div  v-if="slides.length > 0 " class="slide-show" @mouseover="clearInv" @mouseout="runInv">
    <!-- v-for="item in slides" -->
    <div class="slide-img" >
      <a :href="slides[nowIndex].href" >

        <!-- 动画 -->
        <transition name="slide-trans">
          <img v-if="isShow" :src="slides[nowIndex].src" alt="slides[nowIndex].title">
        </transition>
        <transition name="slide-trans-old">
          <img v-if="!isShow" :src="slides[nowIndex].src" alt="slides[nowIndex].title">
        </transition>
      </a>
    </div>
    <h3>{{ slides[nowIndex].title }}</h3>
    <div class="slide-page">
      <ul class="slide-pageUL" >
        <li @click="goto(preIndex)">&lt;</li>
        <li v-for="(item,index) in slides" :key="index" @click="goto(index)">
          <a href="javascript:void(0);return false;" :class="{on:index == nowIndex}">{{index + 1}}</a>
        </li>
        <li @click="goto(nextIndex)">&gt;</li>
      </ul>
    </div>
  </div>
</template>
<script>
  export default {
    // props:子组件接受的什么属性
    props:{
      slides:{
        type:Array,
        default: function(){return []; }//初始值为空
      },
      inv:{//父级传递
        type:Number,
        default:5000  //5s一切换
      }
    },
    data(){

      return{
        nowIndex: 0,
        isShow:true
      }
    },
    computed:{
      preIndex(){
        if(this.nowIndex == 0){
          return this.slides.length-1
        }else{
          return this.nowIndex - 1
        }
      },
      nextIndex(){
        if(this.nowIndex == this.slides.length-1){
          return 0
        }else{
          return this.nowIndex + 1
        }
      }
    },
    methods:{
      goto(index){
        this.nowIndex = index;
        return false;
      },
      //幻灯片自动切换
      runInv(){
        this.invId = setInterval(()=>{
          this.goto(this.nextIndex)
        },this.inv)
      },
      clearInv(){
        clearInterval(this.invId)
      }
    },
    mounted(){
      this.runInv()
    }
  }
</script>
<style scoped>

  .slide-show{
    width: 100%;
    overflow: hidden;
    padding: 10px 10px;
    margin-top: 20px;
  }
  .slide-show h3{
    width: 100%;
    color: #fff;
    background-color: #000;
    opacity: 0.3;
    bottom: 0px;
    font-weight: 500;
    z-index :999;
    margin-top:-30px;

  }
  .slide-img img{
    width: 100%;
    top: 0;
  }
  .slide-page{
    margin-top: -40px;
    padding-left: 55%;
    z-index :9999;
    opacity: 0.9;
  }
  .slide-pageUL .on{
    text-decoration: underline;
  }
  .slide-pageUL li{
    list-style: none;
    float: left;
    display: inline-block;
    padding: 0 10px;
    cursor: pointer;
    color: #fff;
    font-size: 14px;
    height: 32px;
  }
  .slide-page li a{

    display: block;
    float:left;
    color: #fff;
    text-decoration: none;
  }
  .slide-page li:hover{
    color: #1fdd88;
  }
  .slide-page a:hover{
    color: #1fdd88;
  }
</style>



