<template>
	<div>
		<div class="lf-center" :style="{height: htmlProportion * 260 + 'px'}">
			<span class="iconfont lf-icon-checkcircleo text-lllg text-color-blue"></span>
		</div>
		<div class="lf-center" :style="{height: htmlProportion * 40 + 'px'}">  <!-- 实际支付金额 -->
			<span class="text-lg text-w">￥ {{amount / 100}}</span>
		</div>
		<div class="lf-text">
			<div class="lf-between" :style="{height: htmlProportion * 80 + 'px'}">
				<span class="text-grey">订单编号</span>
				<span class="text-sm">{{tradeOrderId}}</span>
			</div>

      <div class="lf-between lf-border-line" :style="{height: htmlProportion * 80 + 'px'}">
        <span class="text-grey">订单金额</span>
        <span class="text-sm">￥ {{orderAmount / 100}}</span>
      </div>

      <div class="lf-between lf-border-line" :style="{height: htmlProportion * 80 + 'px'}">
        <span class="text-grey">优惠金额</span>
        <span class="text-sm">￥ {{discountAmount / 100}}</span>
      </div>

			<div class="lf-between lf-border-line" :style="{height: htmlProportion * 80 + 'px'}">
				<span class="text-grey">支付方式</span>
				<span class="text-sm" v-if="payType == 0">会员卡支付</span>
        <span class="text-sm" v-if="payType == 1">微信支付</span>
        <span class="text-sm" v-if="payType == 2">支付宝支付</span>
			</div>
		</div>
    <ImgSlide :slides="slides"></ImgSlide>

	</div>
</template>

<script>
  import {getAdInfoByPaySuccess} from '../../api/vueAPI.js'
  import ImgSlide from '../../components/ImgSlide'
	export default{
    components:{ImgSlide},
		data() {
			return{
				htmlProportion: '',		//屏幕长宽比例
				htmlWidth: '',		//屏幕宽
				htmlheight: '',		//屏幕高

				payType: -1,  //支付方式 0-会员卡支付  1-微信支付 2-支付宝支付
				amount: 0,  //实际支付金额
        orderAmount: 0,  //订单金额
        discountAmount: 0,  //优惠金额
				tradeOrderId: '',   //订单编号

        slides:[]  //轮播图片数组

			}
		},
		created() {
			this.htmlWidth = document.documentElement.clientWidth || document.body.clientWidth;
			this.htmlheight = document.documentElement.clientHeight || document.body.clientHeight;
			this.htmlProportion = this.htmlWidth / 750 ;
			this.amount = this.$route.query.amount;    //实际支付金额
      this.orderAmount = this.$route.query.orderAmount;    //订单金额
      this.discountAmount = this.$route.query.discountAmount;    //优惠金额
			this.tradeOrderId = this.$route.query.tradeOrderId;  //交易订单号
      this.payType = this.$route.query.payType;   //支付方式


      /** 查询广告配置信息 */
      getAdInfoByPaySuccess(this.tradeOrderId).then(res => {
        if(res.data){

          for(var i = 0; i < res.data.length; i++){

            if(res.data[i].mediaType == 1){   //仅显示图片形式
              this.slides.push({
                src: res.data[i].mediaPath,
                title: "广告",
                href: res.data[i].advertUrl,
              });
            }
          }
        }
      });
		}, methods:{
    }
	}
</script>

<style>
	body{
		background-color: #FFF;
	}
	.text-sm{	/* 14 */
		font-size: 0.875rem;
	}
	.text-lg{	/* 24 */
		font-size: 1.5rem;
	}
	.text-llg{	/* 36 */
		font-size: 2.25rem;
	}
	.text-lllg{	/* 64 */
		font-size: 4rem;
	}
	.text-weight{
		font-weight: 800;
	}
	.text-ff4500{	/* 橙红 */
		color: #ff4500;
	}
	.text-2dc26f{	/* 绿 */
		color: #2dc26f;
	}
	.text-grey{		/* 草灰 */
		color: #a9a9a9;
	}
	.text-red{		/* 红色 */
		color: #E60000;
	}
  .text-color-blue{
    color: #4BA8FF;
  }
	.margin-right-10{
		margin-right: 0.625rem;
	}
	.lf-nav{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		/* height: 80px; */
		background-color: #F5F2F0;
		/* border-bottom: 0.0625rem solid #bdbdc6; */
	}
	.lf-nav-mat{
		width: 100%;
		/* height: 40px; */
	}
	.lf-nav-text{
		width: 100%;
		/* height: 80px; */
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.lf-nav-text > div{
		display: flex;
		align-items: center;
	}
	.lf-nav-text span{
		margin: 0 0.625rem;
	}
	.lf-center{
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.lf-text{
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-content: center;
	}
	.lf-between{
		width: 90%;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.lf-border-line{
		border-bottom: 0.0625rem solid #bdbdc6;
	}
  .adver-ban{
    margin-top: 1.2rem;
    position: relative;
    height: 0;
    width: 100%;
    padding-top: 80%;
  }
  .adver-ban-body{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  .adver-ban-carousel{
    width: 100% !important;
    height: 100% !important;
  }
  .adver-ban-carousel img{
    width: 100%;
    height: 100%;
  }
  .adver-ban-carousel .mu-carousel-indicator-icon {
    width: 7px;
    height: 7px;
  }
  .adver-ban-carousel .mu-carousel-indicator-button{
    width: 7px;
    height: 7px;
    margin: 0 5px;
  }
  .adver-ban-carousel .mu-carousel-indicator-icon{
    opacity: 1;
    background-color: #fff;
  }
  .adver-ban-carousel .mu-carousel-indicator-button__active .mu-carousel-indicator-icon {
    background-color: #4BA8FF;
  }
</style>



